export const SubmitCustomAPI = ( ) => {


  const endpoint = 'https://us1.prisma.sh/public-gingergrabber-650/hackernews-node/dev';

  // const client = new GraphQLClient(endpoint, { headers: { "Authorization": "Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJkYXRhIjp7InNlcnZpY2UiOiJoYWNrZXJuZXdzLW5vZGVAZGV2Iiwicm9sZXMiOlsiYWRtaW4iXX0sImlhdCI6MTUyODQ5NjgxNywiZXhwIjoxNTI5MTAxNjE3fQ.iFlxquLzkyu9R1AjBA3V2-QKAM9BQ1z6gALGUPxzu7M" } });
  const urlInput = document.querySelector('#userURL')
  const descriptionInput = document.querySelector('#urlDescription')
  console.log('url:', urlInput.value, 'description:', descriptionInput.value);

  var xhr = new XMLHttpRequest();
  xhr.responseType = 'json';
  xhr.open("POST", endpoint);
  xhr.setRequestHeader("Content-Type", "application/json");
  xhr.setRequestHeader("Accept", "application/json");
  xhr.setRequestHeader("Authorization", "Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJkYXRhIjp7InNlcnZpY2UiOiJoYWNrZXJuZXdzLW5vZGVAZGV2Iiwicm9sZXMiOlsiYWRtaW4iXX0sImlhdCI6MTUyODQ5NjgxNywiZXhwIjoxNTI5MTAxNjE3fQ.iFlxquLzkyu9R1AjBA3V2-QKAM9BQ1z6gALGUPxzu7M")
  xhr.onload = function () {
    console.log('data returned:', xhr.response);
  }
  xhr.send(JSON.stringify({query: `mutation {
    createLink(data: {
      url: "${urlInput.value}",
      description: "${descriptionInput.value}"
    }) {
      id
    }
  }`}));
}

export default SubmitCustomAPI


