import React from 'react'
import Link from 'gatsby-link'
import CustomLinkForm from '../components/form/form'
const SecondPage = () => (
  <div>
    <h1>Submit a link</h1>
    <p>Want to share a link? Please enter the url and a brief description of why you chose this url.</p>
    <CustomLinkForm />
    <Link to="/">Go back to the homepage</Link>
  </div>
)

export default SecondPage



