import React from 'react'
import SubmitCustomAPI from '../../data/submitLink'

export const CustomRegisterForm = () => (
  <div>
      <form>
        <label for="email">Email
          <input type="email" placeholder="you@xdomain.com" id="email" name="email" />
        </label>
        <label for="name">Full Name
          <input type="text" placeholder="Enter full name" id="name" name="name" />
        </label>
        <label for="userURL">URL
          <input type="url" placeholder="Enter URL" id="userURL" name="userURL" />
        </label>
        <label for="urlDescription">Description
          <textarea type="text" placeholder="Enter description" id="urlDescription" name="urlDescription"></textarea>
        </label>
      </form>
        <button onClick={SubmitCustomAPI}>Submit</button>
    </div>
)

